<template>
    <v-layout wrap py-5>
        
        <v-flex v-for="(content, index) in collection_display" :key="index" lg3 sm6 xs12>
          <v-card class="mb-5 px-2" flat>

            <v-card 
                :color="getColor[index]" 
                height="150" 
                class="d-flex pa-2 align-self-end"
            >
                <div class="align-self-end">
                    <h3 class="body-1 white--text px-2">{{content.title}}</h3>
                    <v-btn @click="redirectToGallery(content.id, content.title)" x-small text dark>view more</v-btn>
                </div>
            </v-card>
          </v-card>
        </v-flex>

        
    </v-layout>
</template>

<script>
import { mapState } from 'vuex'

export default {
    name: 'Collection-container',

    computed: {
        ...mapState(
                [
                    'home_collection_items', 
                    'collection_items', 
                    'home_gallery_content', 
                    'gallery_content'
                ])
            ,


        collection_display () {
            if (this.$route.name === 'Home') return this.home_collection_items
            return this.collection_items
        },

        
        getColor() {
            let get_content_from = this.gallery_content

            if (this.$route.name === 'home'){
                get_content_from = this.home_gallery_content
            } 

            const get_gallery_color = get_content_from.map(items => items.avg_color)

            return get_gallery_color
        },
    },

    methods: {
        redirectToGallery(id, title) {
            return this.$router.push(`/collection?id=${id}&title=${title}`)
        },
    }


}
</script>