<template>
   <v-container fluid class="footer pb-0 px-0">
      <v-container>
         <v-row>
            <v-col md="4" cols="12">
               <v-card-title class="text-font font-weight-medium white--text mb-1 pl-0" style="font-size:24pt">El Gallery</v-card-title>
               <v-card-subtitle class="white--text text-font pl-0">Explore Visionary Photography</v-card-subtitle>

               <social-links :dark="true" class="px-0" />
            </v-col>

            <v-col md="4" cols="12">
               <v-card-title class="text-font font-weight-medium white--text mb-1 pb-0 pl-0" style="font-size:16pt">Navigations</v-card-title>
               
               <v-flex v-for="(nav, index) in navigation" :key="index" xs12>
                  <v-card-subtitle
                     @click="navigateTo(nav.link)"
                     style="cursor: pointer !important"
                     class="white--text pl-0 py-2"
                     text
                     small
                     >
                     {{nav.title}}
                  </v-card-subtitle>
               </v-flex>

            </v-col>

            <v-col md="4" cols="12">
               <v-card-title class="text-font font-weight-medium white--text mb-1 pb-0 pl-0" style="font-size:16pt">Message Me</v-card-title>
               
               <v-form @submit.prevent="submit" ref="form">
                  <v-text-field 
                     v-model="name"
                     :rules="rules"
                     name="name"
                     type="text"
                     label="Name"
                     maxLength="30"
                     filled 
                     dark>
                  </v-text-field>

                  <v-text-field 
                     v-model="email"
                     :rules="email_rules"
                     name="email"
                     type="email"
                     label="Email"
                     maxLength="30"
                     filled 
                     dark>
                  </v-text-field>

                  <v-textarea 
                     v-model="message"
                     :rules="rules"
                     name="message"
                     label="Message"
                     maxLength="300"
                     filled 
                     dark>
                  </v-textarea>

                  <v-btn :loading="loading" type="submit" dark block> Send Message <v-icon right>mdi-send</v-icon></v-btn>
               </v-form>


               <v-snackbar 
                  v-model="snackbar"
                  :color="snackbar_message === 'Message submitted successfully' ? 'success' : 'error' "
                  timeout="3000"
                  dark>
                     {{ snackbar_message }}
                  <template v-slot:action="{ attrs }">
                     <v-btn  v-bind="attrs" @click="snackbar = false" color="white--text" text>
                        Close
                     </v-btn>
                  </template>
               </v-snackbar>
            </v-col>

         </v-row>
      </v-container>

      <v-col cols="12" class="mt-5" style="background: #272727">
         <h1 class="body-2 font-medium text-center white--text">© 2023 Copyright: El Gallery</h1>
      </v-col>
   </v-container>
</template>

<script>
import axios from 'axios'
import SocialLinks from './Navigation/SocialLinks.vue'

export default {
   name: 'FooterView',
   components: {SocialLinks},
   data() {
      return {
         
         navigation: [
            {
               title: 'Home',
               link: '/',
            },
            {
               title: 'Gallery',
               link: '/gallery',
            },
            {
               title: 'Collection',
               link: '/collection',
            },
            {
               title: 'Video',
               link: '/video',
            },
         ],

         rules: [
            v => !!v || 'Name is required',
         ],

         email_rules: [
            v => !!v || 'E-mail is required',
            v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
         ],
         

         name: '',
         email: '',
         message: '',
         loading: false,

         snackbar: false,
         snackbar_message: '',
      }
   },

   methods: {
      navigateTo(link) {
         if (this.$route.fullPath === link) return
         return this.$router.push(link)
      },

      async submit() {
         if (this.$refs.form.validate()) {
            this.loading = true
            try {
               const is_successful = await axios.post('https://api.apispreadsheets.com/data/1lR5ScmwokCoy7vk/', JSON.stringify({
                  name: this.name,
                  email: this.email,
                  message: this.message,
               }))
               this.$refs.form.reset();
               this.loading = false
               this.snackbar = true

               if (is_successful.status !== 201) return  this.snackbar_message = 'Failed to send the message'

               return this.snackbar_message = 'Message submitted successfully'

            } catch (err) {
               this.$refs.form.reset();
               this.loading = false
               console.log(err)
            }
         }
      }
   }
}
</script>

<style scoped>
.footer {
   background: #2d3029;
}
</style>