<template>
    <v-dialog v-model="open_dialog"  v-if="photo_content" width="1200" >
        <v-flex md12>
            <v-card>
                <v-layout wrap>
                    <v-sheet   
                        v-show="image_height === '600px'"
                        :height="image_height"
                        class="col-md-8 pa-0" >
                            <v-img
                                :src="photo_content.src.large"
                                height="600px">
                            </v-img>
                    </v-sheet>

                    <v-sheet   
                        v-show="image_height !== '600px'"
                        class="col-md-8 pa-0">
                            <v-img
                                :src="photo_content.src.landscape">
                            </v-img>
                    </v-sheet>
                    
                    <v-flex md4 align-self-center>
                        <v-card-title>{{photo_content.alt}}</v-card-title>
                        <v-card-subtitle>by: {{photo_content.photographer}}</v-card-subtitle>
                    </v-flex>
                </v-layout>
            </v-card>
        
        </v-flex>
    </v-dialog>
</template>

<script>
export default {
    props: ['photo_content'],
    data() {
        return {
            open_dialog: false
        }
    },

    computed: {
        image_height () {
            
            let height = '600px'
      
            if (this.$vuetify.breakpoint.name === 'xs' || this.$vuetify.breakpoint.name === 'sm') {
                height = '100%'
            } else {
                height = "600px"
            }

            
            return height
        }

    }
}
</script>

<style scoped>
.img-display {
    width: 100;
    max-height: 100%;
    max-width: 100%;
    object-fit: cover;
 }
</style>