<template>
    <v-flex xs12>
      <v-card  v-for="data in home_data" :key="data.name"  width="auto" class="mb-10" flat>
          <content-container :title="data.name" :link="data.link">
              <gallery-container v-if="data.name === 'Gallery'" @view-photo="viewPhoto"/>
              <collection-container v-if="data.name === 'Collection'"/>
              <video-container v-if="data.name === 'Video'" @view-video="viewVideo" />
          </content-container>
       </v-card>
       
      <photo-view :photo_content="photo_content" ref="photo_view"/>
    </v-flex>
</template>

<script>
import ContentContainer from '../components/Home/MainContentContainer.vue'
import GalleryContainer from '../components/Home/HomeGalleryContainer.vue'
import CollectionContainer from '../components/Home/HomeCollectionContainer.vue'
import VideoContainer from '../components/Home/HomeVideoContainer.vue'
import PhotoView from '@/components/PhotoView.vue'


export default {
  name:'Home-view',
  props:['test_data'],
  components: {
      ContentContainer, 
      GalleryContainer, 
      PhotoView,
      CollectionContainer,
      VideoContainer
  },

  data() {
    return {
      home_data:[
        {
          name: 'Gallery',
          link: '/gallery'
        },
        {
          name: 'Collection',
          link: '/collection'
        },
        {
          name: 'Video',
          link: '/video'
        },
      ],

      photo_content: null,
    }
  },

  methods: {
    viewPhoto(data) {
      this.photo_content = data
      this.$refs.photo_view.open_dialog = true
    },
  }

}
</script>

