<template>
    <v-flex xs12>
        <v-flex xs12>
            <VideoContent :video_src="video_playing" :show_controls="true" />
            <h3 class="title mt-5 mb-0"><span class="font-weight-regular">By: </span> {{user_name}}</h3>
            <h4 class="subtitle-1">Visit link: <a :href="user_link" target="_blank">{{user_link}}</a></h4>
        </v-flex> 

        <video-container @play-video="playVideo" />
    </v-flex>
</template>

<script>
import { mapState } from 'vuex'

import VideoContainer from '../components/Home/HomeVideoContainer.vue'
import VideoContent from '../components/VideoContent.vue'

export default {
    name: 'VideoView',

    components: {
        VideoContainer,
        VideoContent
    },

    data() {
        return {
            video_playing: '',
            user_name: '',
            user_link: '',
        }
    },

    mounted() {
        this.validatePage();
    },

    computed: {
        ...mapState(['video_content', 'searched_video']),

    },


    methods: {
        async validatePage() {
            if (this.video_content.length === 0) {
                return setTimeout(() => this.validatePage(), 3000)
            } 

            if (this.$route.query.id) {
                const {src, name, link} =  await this.getVideoSrc(this.$route.query.id)
                this.video_playing = src
                this.user_name = name
                this.user_link = link
                return 
            } else {
                this.video_playing = this.video_content[0].video_files[0].link
                this.user_name = this.video_content[0].user.name
                this.user_link = this.video_content[0].user.url
                return 
            }
        },

        async getVideoSrc(id) {
            let data = null
            const filtered_item = this.video_content.filter(item => item.id === Number(id))
            if (filtered_item.length === 0) {
                await this.$store.dispatch('searchSpecificVideo', id)
                data = {
                    src:  this.searched_video.video_files[0].link,
                    name: this.searched_video.user.name,
                    link: this.searched_video.user.url
                }
            } else {
                data = {
                    src:  filtered_item[0].video_files[0].link ,
                    name: filtered_item[0].user.name,
                    link: filtered_item[0].user.url
                }
            }

            return data
        },

       async playVideo(id) {
            const {src, name, link} = await this.getVideoSrc(id)
            this.video_playing = src
            this.user_name = name
            this.user_link = link
        }
    }
}
</script>