<template>
  <v-app>
    <v-main>
      <banner-container v-once></banner-container>
      
      <v-container class="py-15" id="main-container" fluid>
        <v-row>
            
          <app-navigation v-once class="white"/>

          <v-col lg="9" md="8">
            <router-view :test_data="test_data"/>
          </v-col>
          
        </v-row>
      </v-container>
  
      <footer-navigation />
    </v-main>
  </v-app>
</template>

<script>
import BannerContainer from './components/BannerContainer.vue'
import AppNavigation from './components/Navigation/AppNavigation.vue'
import FooterNavigation from './components/FooterNavigation.vue'


export default {
  name: 'App',
  components: {BannerContainer, AppNavigation, FooterNavigation},
  data() {
    return {
      test_data: 'testinger'
    }
  },

  created() {
    this.getContent();
    this.getCollection();
    this.getContentVideo();
    
  },

  methods: {
    getContent() {
      this.$store.dispatch('getMainContent')
    },

    getCollection() {
      this.$store.dispatch('getCollectionItems')
    },

    getContentVideo() {
      this.$store.dispatch('getMainContentVideo')
    },

  }
};
</script>


