<template>
    <v-layout wrap py-5>
        <v-flex v-for="(content, index) in video_display" :key="index" lg3 sm6 xs12 >
         
          <v-flex xs12 px-1>
            <v-hover  v-slot="{hover}">
              <VideoContent
                @click="showVideo(content.id)"
                @mouseover="playVideo(index)"
                @mouseout="pauseVideo(index)"
                :class="[hover ? 'elevation-15' : 'elevation-3']"
                :video_src="content.video_files[0].link"
                :video_index="index"
                :ref="`video_${index}`"
              />
            </v-hover>

            <div class="mb-3">
              <h3 class="subtitle-2 text-center mt-2 mb-0">
                  <span class="font-weight-regular">By: </span>  
                  {{content.user.name}}
              </h3>
            </div>
          </v-flex>

        </v-flex>
    </v-layout>
</template>


<script>
import { mapState } from 'vuex'

import VideoContent from '../VideoContent.vue'

export default {
  name:'Video-container',
  emits:['play-video'],

  components: {
    VideoContent
  },

  computed:{
    ...mapState(['home_video_content', 'video_content']),

    video_display() {
      if (this.$route.name === 'Home') return this.home_video_content

      return this.video_content
    }

  },

  methods: {
   playVideo(id) {
     this.$refs[`video_${id}`][0].$refs.video.muted = true;
     this.$refs[`video_${id}`][0].$refs.video.play();
    },

    pauseVideo(id) {
       this.$refs[`video_${id}`][0].$refs.video.muted = false;
       this.$refs[`video_${id}`][0].$refs.video.pause();
       this.$refs[`video_${id}`][0].$refs.video.currentTime = 0;
    },

    getVideo(content) {
       this.$emit('view-video', content)
    },

    showVideo(id) {
        if (this.$route.name === 'Home') {
          return this.$router.push(`/video?id=${id}`)
        } else {
          return this.$emit('play-video', id)
        }
    }
  }

}
</script>
