<template>
    <v-flex lg12>
      <v-card class="mb-5 px-2 video-container">
            <video
               @click="$emit('click')"
               @mouseover="$emit('mouseover')"
               @mouseout="$emit('mouseout')"
               :controls="controls"
               :src="video_src"
               ref="video"
               class="video-player">
            </video>
      </v-card>
   </v-flex>
</template>

<script>
export default {
  name: 'VideoContent',
  props: {
    video_src: {
      type: String,
      required: true,
    },

    show_controls: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    controls() {
      return this.show_controls;
    }
  },
}
</script>

<style>
.video-container {
  position: relative;
  width: 100%;
  padding-bottom: 56.25%; /* 16:9 aspect ratio */
}

.video-player {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;
}

</style>