<template>
    <v-layout wrap py-5>
    
        <v-flex v-for="(content, index) in photo_display" :key="index" lg3 sm6 xs12>
          
          <v-card class="mb-5 px-2"  flat>
                <v-hover  v-slot="{hover}">
                    <v-img
                      @click="getPhoto(content)"
                      :src="content.src.large"
                      :elevation="hover ? 15 : 3"
                      :class="[hover ? 'elevation-15' : 'elevation-3']"
                      height="200"
                      style="cursor: pointer"
                    ></v-img>
                  </v-hover>


                <div v-show="$route.name === 'Home'">
                  <h3 class="subtitle-2 text-center mt-5 mb-0">{{content.alt }}</h3>
                  <h3 class="body-2 text-center mb-0">{{content.photographer }}</h3>
                </div>
          </v-card>
   

              
        </v-flex>
    </v-layout>
</template>


<script>
import { mapState } from 'vuex'


export default {
  name:'Gallery-container',
  emits:['view-photo'],

  data() {
    return {
     
    }
  },

  
  computed:{
    ...mapState(['home_gallery_content', 'gallery_content', 'collection_display']),

    photo_display() {
      if (this.collection_display.length > 0) return this.collection_display
      if (this.$route.name === 'Home') return this.home_gallery_content

      return this.gallery_content
    }

  },

  methods: {
    getPhoto(content) { 
      this.$emit('view-photo', content)
    }
  }
}
</script>