<template>
    <section class="banner-container">
        <v-carousel
            class="carousel"
            height="100%"
            cycle
            show-arrows-on-hover
            hide-delimiter-background
        >
            <v-carousel-item
                v-for="(slide, i) in banner_content"
                :key="i"
                class="carousel-item"
                eager
            >
                <v-parallax :src="slide.src.large2x">
                <!-- <v-parallax src="https://cdn.vuetifyjs.com/images/parallax/material.jpg"> -->
                    <v-row  class="parallax-container" align="center" justify="center">
                        <v-col cols="12" class="parallax-content text-center">
                            <h1 class="text-font"> El Gallery</h1>
                            <h2 class="text-font mb-10 font-weight-medium" style="font-size:16pt !important">Explore Visionary Photography</h2>
                            <v-btn 

                                @click="$vuetify.goTo('#main-container')"
                                v-bind="btn_size"
                                class="text-font" 
                                color="white"
                                outlined 
                                tile>
                                Click for More
                            </v-btn>   
                        </v-col>
                    </v-row>
                </v-parallax>
            </v-carousel-item>
        </v-carousel>
    </section>
</template>

<script>
import { mapState } from 'vuex';
export default {
    app: 'banner-container',
    data() {
        return {
            slides: [1],
        }    
    },

    computed: {
        ...mapState(['banner_content']),

        btn_size () {
            const size = {sm:'large',md: 'large',lg:'x-large',xl:'x-large'}[this.$vuetify.breakpoint.name];
            return size ? { [size]: true } : {}
        }
    },
}
</script>

<style scoped>
::v-deep .v-parallax{
    height: 100% !important;
}

::v-deep .v-parallax__content {
    padding:0
}

.banner-container {
    height:100vh;
}

.parallax-container {
    height: 100%;
    background:#8b8b8b6b;
}

.parallax-content h1{
    font-size: 85pt;
    font-weight: 300;
    margin-bottom: 0;
}

.parallax-content h4{
    font-size: 20pt
}

@media screen and (max-width: 599px) {
    .parallax-content h1 {
        font-size: 40pt;
    }
}

@media screen and (min-width: 600px) and (max-width: 960px) {
    .parallax-content h1 {
        font-size: 70pt;
    }
}
</style>