<template>
    <v-flex xs12>
         <collection-container v-if="!this.$route.query.id"/>

         <v-flex v-else xs12 class="">
      
            <v-layout wrap justify-space-between>
                
                    <h2 class="font-weight-regular text-font ml-2">{{this.$route.query.title}}</h2>
                    <v-btn @click="$router.push('/collection')"  text small>Browse other collection</v-btn>
               
            </v-layout>

            <div v-if="collection_display_loader " class="text-center">
                <v-progress-circular class="my-15" rotate indeterminate ></v-progress-circular>
            </div>

            <div v-else>
                <gallery-container @view-photo="viewPhoto" />
                <photo-view :photo_content="photo_content" ref="photo_view"/>
            </div>
         </v-flex>
    </v-flex>
</template>

<script>
import CollectionContainer from '../components/Home/HomeCollectionContainer.vue'
import GalleryContainer from '../components/Home/HomeGalleryContainer.vue'
import PhotoView from '@/components/PhotoView.vue'
import { mapState } from 'vuex'

export default {
    name: 'Collections-view',
    components: {CollectionContainer, GalleryContainer, PhotoView},
    data() {
        return {
            display_main: true,
            photo_content: null,
        }
    },

    mounted() {
        this.validatePage();
    },

    computed: {
        ...mapState(['collection_display_loader'])
    },

    watch: {
        '$route' (to){
           if (to.query.id) {
                return this.callCollectionDisplay(this.$route.query.id)
           }
           return this.display_main = true
        }
    },

    methods: {
        validatePage() {
            if (!this.$route.query.id) return this.display_main = true
            return this.callCollectionDisplay(this.$route.query.id)
        },

        async callCollectionDisplay(id) {
            this.display_main = false 
            return await this.$store.dispatch('getCollectionDisplay', id)
        },

        viewPhoto(data) {
            this.photo_content = data
            this.$refs.photo_view.open_dialog = true
        }
    },

    beforeDestroy() {
        this.$store.commit('setCollectionDisplay', [])
    }

}
</script>