import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

//create a function
const getItemFromArray = (count, data) => {
  const shuffled = [...data].sort(() => 0.5 - Math.random())
  const content = shuffled.slice(0, count)
  return content
}

export default new Vuex.Store({
  state: {
    banner_content: [],

    home_gallery_content: [],
    home_collection_items: [],
    home_video_content:[],

    gallery_content: [],

    collection_items: [],
    collection_display: [],
    collection_display_loader: false,
    
    video_content:[],
    video_content_loader: false,
    searched_video:[],
  },


  mutations: {
    setBannerContent(state, payload) {
      state.banner_content = payload
    },

    setHomeContent(state, {type, payload}) {
      if (type === 'gallery') return state.home_gallery_content = payload;
      if (type === 'collection') return state.home_collection_items = payload;
      return state.home_video_content = payload;
    },

    setGalleryContent(state, payload) {
      state.gallery_content = payload
    },

    setCollectionItems(state, payload) {
      state.collection_items = payload
    },


    setDisplayLoader(state, {type, payload}) {
      if (type === 'collection') return state.collection_display_loader = payload
      state.video_content_loader = payload
    },
    
    setCollectionDisplay(state, payload) {
      state.collection_display = payload
    },

    setVideoContent(state, payload) {
      state.video_content = payload
    },

    setSpecificVideo(state, payload) {
      state.searched_video = payload
    }

  },
  actions: {
    async getMainContent({dispatch, commit}) {
      try {
        const get_random_page = Math.round((Math.random() * (100) + 1))
        const content = await axios.get(`/v1/curated/?page=${get_random_page}&per_page=15`)

        // Adjust the alt name of the photos
        const updated_photo = content.data.photos.map(item => {
          if (item.alt.includes('Free stock photo of')) {
            item.alt = `A photo of ${item.alt.substr(19)}`
          }
  
          if (item.alt == '') {
            item.alt = 'Untitled'
          }
          
          return item
        })

        // Call getBannerContent
        dispatch('getBannerContent', updated_photo)

        // Call getHomeContent
        dispatch('getHomeContent', {type: 'gallery' , content: updated_photo})

        // Commit Gallery Content
        commit('setGalleryContent', updated_photo);
        return 
      } catch (err) {
        console.log(err)
      }
    },

    async getCollectionItems ({dispatch, commit}) {
      try {
        const get_random_page = Math.round((Math.random() * (52) + 1))
        const collection_items = await axios.get(`/v1/collections/featured/?page=${get_random_page}&per_page=10`)

        // Call getHomeContent
        dispatch('getHomeContent', {type: 'collection', content: collection_items.data.collections})

        commit('setCollectionItems', collection_items.data.collections)
      } catch (err) {
        console.log(err)
      }
    },

    async getMainContentVideo({dispatch, commit}) {
      try {
        const get_random_page = Math.round((Math.random() * (30) + 1))
        const content = await axios.get(`/videos/popular/?page=${get_random_page}&per_page=15`)
        
        // Call getHomeContent
        dispatch('getHomeContent', {type: 'videos' , content: content.data.videos})
        
        commit('setVideoContent', content.data.videos)
        return 
      } catch (err) {
        console.log(err)
      }
    },

    async getCollectionDisplay({commit}, id) {
      try {
  
        commit('setDisplayLoader',  {type: 'collection', payload: true})
        const data_collection = await axios.get(`/v1/collections/${id}?per_page=15`)

        setTimeout(() => {
          commit('setDisplayLoader',  {type: 'collection', payload: false})
        },0.1)

        const collection_data = data_collection.data.media.filter(items => items.type === 'Photo')
        commit('setCollectionDisplay', collection_data)
      } catch (err) {
        console.log(err)
      }
    },
    

    async searchSpecificVideo({commit}, id) {
      try {
          const searched_data = await axios.get(`/videos/videos/${id}`)
          commit('setSpecificVideo', searched_data.data)
          return true
      } catch (err) {
        console.log(err)
        return false 
      }
      
    },

    // Used for getting 3 data display for the banner
    getBannerContent({commit}, banner_content) {
      const content = getItemFromArray(3, banner_content)
      commit('setBannerContent', content)
    },
    
    // Used for getting 4 data for display in the home page section for gallery
    getHomeContent({commit}, {type, content}) {
      const data = getItemFromArray(4, content)

      const payload = {type, payload: data}

      return commit('setHomeContent', payload)
    },
    
  },

})
