<template>
    <v-flex class="d-md-inline d-sm-flex justify-end">
        <v-expand-transition  v-for="item in items" :key="item.title">
            <v-flex v-show="expand" @click="$router.push(item.route)">   
                <div class="nav-item-container py-2 px-5 mb-md-3 text-md-left text-sm-center">
                    <h3 class="font-weight-light">{{item.title}}</h3>

                    
                    <v-slide-x-transition>
                        <div 
                            :class="[{'active-nav' : item.title == $route.name}, 'non-active-nav mx-md-0 mx-sm-auto']" >
                        </div>
                    </v-slide-x-transition>
                </div>
            </v-flex>
        </v-expand-transition>
    </v-flex>
</template>

<script>
    export default {
        name: 'navigation-list',
        props: ['items', 'expand'],
     

        mounted() {
          if(this.$vuetify.breakpoint.name === 'xs') this.$emit('change-expand', false)
        },

        watch: {
            '$vuetify.breakpoint.name'() {
                if (this.$vuetify.breakpoint.name === 'xs') return this.$emit('change-expand', false)
                return this.$emit('change-expand', true)
            }
        }
    }
</script>


<style scoped>
.nav-item-container {
    cursor: pointer;
}

.nav-item-container:hover{
    background: #ededed;
}

.active-nav { 
    background:#FFC0C0 !important; 
    width:60px;
    height:5px;
}
.non-active-nav { 
    background:transparent; 
    width:60px;
    height:5px;
}
</style>